.hero-image {
    position: relative;
    width: 100%;
    height: 50vh;
    background: rgba(0, 0, 0, .3);
}

.hero-image::before {
    content: '';
    background: url('../assets/heroImg.jpg') no-repeat center center/cover;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.hero-image .heading {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero-image h1 {
    font-size: 2.4rem;
}

.hero-image p {
    font-size: 1.4rem;
}

@media screen and (max-width: 640px) {
    .hero-image h1 {
        font-size: 2rem;
    }
}